.carousel-contest-2 {
    .overlay {
        left: -90px;
        top: -10px;
        @media #{$tablet} {
            right: 543px;

        }
        @media #{$desktop} {


        }
        @media #{$desktop-large} {


        }
        img {
            @media #{$phone-only, $phone-wide} {
                height: 40%;
                position: relative;
                top: 50px;
                left: 115px;
                -webkit-transform: rotate(-15deg);
                transform: rotate(-15deg);
            }
            @media #{$tablet} {
                height: 32%;
                position: relative;
                top: 135px;
                left: 120px;
                -webkit-transform: rotate(-15deg);
                transform: rotate(-15deg);
                &:lang(fr) {
                    left: 90px;
                }
            }
            @media #{$desktop} {
                height: 42%;
                position: relative;
                top: 195px;
                left: 100px;
                -webkit-transform: rotate(-15deg);
                transform: rotate(-15deg);
            }
            @media #{$desktop-large} {
                height: 52%;
                position: relative;
                top: 210px;
                left: 80px;
                transform: rotate(-15deg);
            }
        }
    }
    .carousel-icon {
        height: 70px;
        width: 105px;
        margin-right: 0;
        margin-left: auto;
        @media #{$phone-wide} {
            margin-right: auto;
        }
        @media #{$desktop} {
            width: 160px;
            height: 105px;
        }
    }
    .content {
        text-align: center;
        left: auto;
        right: 30px;
        @media #{$phone-only} {
            text-align: right;
            width: 40%;
            top: 90px;
            left: auto;
            right: 30px;
            &:lang(fr) {
                top: 70px;
            }
        }
        @media #{$phone-wide} {
            top: 85px;
        }
        @media #{$tablet} {
            width: 50%;
            top: 80px;
            &:lang(fr) {
            }
        }
        @media #{$desktop} {
            width: 50%;
            top: 80px;
            left: 434px;
            &:lang(fr) {
                top: 60px;
                width: 50%;
            }
        }
        @media #{$desktop-large} {
            text-align: center;
            width: 64%;
            top: 125px;
            left: 430px;
            &:lang(fr) {
                text-align: center;
                width: 64%;
                top: 115px;
                left: 430px;
            }
        }
        h1 {
            @media #{$desktop-large} {
                width: 550px;
                &:lang(fr) {
                  width: 500px;
                }
            }
        }
        p {
            @media #{$desktop} {
                margin: auto auto 15px auto;
                font-size: 20px;
                &:lang(fr) {
                  width: 390px;
                }
            }
        }
        .button {
            @media #{$tablet} {
                &:lang(fr) {
                    width: 310px;   
                }
            }
            @media #{$desktop} {
                width: 310px;
                &:lang(fr) {
                    width: 310px;
                }
            }
        }
    }
}
