.carousel-investor {
    .overlay {
        right: -130px;
        top: -10px;
        @media #{$tablet} {
            right: -210px;

        }
        @media #{$desktop} {
            right: -90px;

        }
        @media #{$desktop-large} {


        }
        img {
            @media #{$tablet} {
                height: 80%;
            }
            @media #{$desktop} {
                height: 100%;
            }
        }
    }
    .carousel-icon {
        height: 80px;
        @media #{$desktop} {
            width: 63px;
            height: 107px;
        }
    }
    .content {
        @media #{$tablet} {
            width: 40%;
            top: 50px;
            left: 50px;
            &:lang(fr) {
                width: 50%;
            }
        }
        @media #{$desktop} {
            width: 54%;
            top: 30px;
            right: auto;
            left: 30px;
        }
        @media #{$desktop-large} {
            width: 64%;
            top: 80px;
            right: auto;
            left: 10px;
        }
        h1 {
            @media #{$tablet} {
                &:lang(fr) {
                    margin: 8px 40px 10px 40px;
                }
            }
            @media #{$desktop} {
                width: 545px;
            }
        }
        p {

            @media #{$tablet} {
                &:lang(fr) {
                    margin: auto auto 8px auto;
                }
            }
            @media #{$desktop} {
                &:lang(fr) {
                    margin: auto auto 16px auto;
                    font-size: 16px;
                }
            }
            @media #{$desktop-large} {
                &:lang(fr) {
                    font-size: 18px;
                }
            }
        }
        .button {

            &:lang(fr) {

            @media #{$tablet} {
                height: auto;
            }

            @media #{$desktop} {
            width: 320px;
            height: auto;
            text-align: center;
    }
            @media #{$desktop-large} {
            width: 320px;
            height: auto;
            text-align: center;
    }


                }

            @media #{$phone-wide} {}
            @media #{$tablet} {
                width: 300px;
                font-size: 14px;
                display: block;
                height: 42px;
                padding-top: 10px;
            }
            @media #{$desktop} {
                width: 250px;
                display: block;
                margin: 10px auto;
                text-align: left;
                font-weight: bold;
                font-size: 16px;
            }
            @media #{$desktop-large} {}
        }
    }
}
