.heading-wrapper {
    &:before,
    &:after {
        content: " ";
        display: table;
        clear: both;
    }
    margin-top: 40px;
    margin-bottom: 0px;
    @media #{$tablet} {
        margin-top: 100px;
        margin-bottom: 30px;
    }
    .step-circle {
        margin: auto;
        @media #{$tablet} {
            float: left;
        }
    }
    h2 {
        text-align: center;
        margin-bottom: 0;
        margin-top: 12px;
        @media #{$tablet} {
            float: left;
            margin: 2em 0 1.4em 0;
            margin-left: 30px;
        }
    }
}

.one-third-icon {
    width: 60%;
    display: block;
    margin:  auto;
    margin: 20px auto 10px auto;
    &.calendar-coin {
        width: 84px;
        @media #{$tablet} {
            width: 115px;
            margin: 29px auto 20px auto;
        }
        @media #{$desktop} {
            width: 128px;
        }
    }
    &.gauge {
        width: 74px;
        @media #{$tablet} {
            width: 100px;
            margin: 15px auto 20px auto;
        }
        @media #{$desktop} {
            width: 112px;
        }
    }
    &.employee {
        width: 46px;
        @media #{$tablet} {
            width: 66px;
            margin: 13px auto 20px auto;
        }
        @media #{$desktop} {
            width: 74px;
        }
    }
    &.hour-counter {
        width: 130px;
        @media #{$tablet} {
            width: 171px;
            margin: 40px auto 20px auto;
        }
        @media #{$desktop} {
            width: 194px;
        }
    }
    &.calendar-may-one {
        width: 36px;
        @media #{$tablet} {
            width: 48px;
            margin: 43px auto 20px auto;
        }
        @media #{$desktop} {
            width: 56px;
        }
    }
    &.trophy-june-july {
        width: 130px;
        @media #{$tablet} {
            width: 162px;
            margin: 4px auto 20px auto;
        }
    }
    &.decthirtyone-employee {
        width: 87px;
        @media #{$tablet} {
            width: 109px;
            margin: 7px auto 20px auto;
        }
    }
}

.icon-wrapper {
    img {
        display: block;
        margin: auto;
    }
    .sixty-five-circle {
        width: 66px;
    }
    .exit-ct {
        width: 46px;
    }
    .retirement-sign {
        width: 94px;
    }
    .ct-building {
        width: 58px;
    }
    .money-house {
        width: 63px;
    }
    .laptop {
        width: 61px;
    }
    .hold-money {
        width: 66px;
    }
    .money-speech {
        width: 54px;
    }
    .ct-certificate {
        width: 76px;
    }
    .fifty-pie {
        width: 48px;
    }
}

.icon-support {
    text-align: center;
    padding: 0 15px;
}

.plus-icon {
    width: 20px;
    height: 20px;
    background-image: url(../img/icon-plus.png);
    background-size: 20px 20px;
    margin: 20px auto;
    
    @media #{$phone-wide} {
        position: absolute;
        right: -14px;
        top: 17px;
        margin: 0;
        background-size: 20px 20px;
    }
    @media #{$tablet} {
        top: 50px;
        width: 27px;
        height: 27px;
        background-size: 27px 27px;
    }
}

.plan-item {
    text-align: center;
    margin: 0px 0 40px 0;
    @media #{$desktop} {
        margin: 40px 0;
    }
    h4 {
        font-size: 16px;
        text-transform: none;
        letter-spacing: normal;
        font-weight: bold;
        &:after {
            content: " ";
            background-image: url(../img/gradient-divider.png);
            height: 3px;
            width: 100%;
            display: block;
            background-repeat: no-repeat;
            background-position: center;
        }
        @media #{$desktop} {
            font-size: 18px;
        }
    }
}