.related-links {
    background-color: #f4f4f4;
    border-top: 1px solid #d1d3d4;
    position: relative;
    top: 30px;
    padding: 33px 0;
    font-size: 14px;
    a {
        font-weight: normal;
    }
    .arrow-links {
        ul {
            list-style: none;
            padding:0; 
            margin-bottom: 50px;
            @media #{$tablet} {
                margin-bottom: 10px;
            }
            li {
                margin: 1em 0;
            }
        }
    }
}