.trackrecord-tile {
    background-color: #ccc;
    padding: 20px 0;
    position: relative;
    background-size: cover;
    background-position: top;
    margin-bottom: 30px;
    &.latest-record {
        overflow: hidden;
    }
    .latest-tag {
        position: absolute;
        top: -9px;
        right: -10px;
        width: 90px;
        @media #{$tablet} {
            width: 140px;
        }
        img {
            width: 100%;
        }
    }
    .trackrecord-cover {
        @include box-shadow(0px 1px 5px rgba(0,0,0,0.4));
        float: left;
        display: block;
        width: 36px;
        @media #{$phone-wide} {
            width: 98px;
        }
        @media #{$tablet} {
            width: 124px;
        }
        @media #{$desktop} {
            width: 146px;
        }
        @media #{$desktop-large} {
            width: 163px;
        }
    }
    h3 {
        color: #fff;
        text-transform: uppercase;
        font-weight: bold;
        font-size: 30px;
        margin-top: 0;
        margin-bottom: 0.5em;
    }
    .btn-download {
        right: 20px;
        bottom: 20px;
        margin-top: 5px;
        margin-left: auto;
        margin-right: auto;
        display: block;
        clear: both;
        max-width: 280px;
        position: static;
        @media #{$tablet} {
            position: absolute;
        }
    }
    .right-content {
        float: left;
        margin-left: 15px;
        width: 70%;
        @media #{$tablet} {
            margin-left: 20px;
            width: 56%;
        }
        @media #{$desktop} {
            width: 55%;
        }
    }
    p {
        color: #fff;
    }
    ul {
        color: #fff;
        li {
            margin-bottom: 0;
        }
    }
    
    &.winter-16 {
        background-image: url(../img/bg-trackrecord-winter-2016.jpg)
    }
    &.summer {
        background-image: url(../img/bg-trackrecord-summer.jpg);
    }
    &.winter {
        background-image: url(../img/bg-trackrecord-winter.jpg);
    }
    &.fall {
        background-image: url(../img/bg-trackrecord-fall.jpg);
    }
}