.carousel-wrapper {
    background-color: #ECECEC;
    position: relative;
    overflow: hidden;
    height: 308px;
    /*    display: none;*/
    @media #{$phone-wide} {
        height: 302px;
    }
    @media #{$tablet} {
        height: 360px;
        margin-top: -33px;
        .container {
            width: 100%;
        }
    }
    @media #{$desktop} {
        padding-top: 30px;
        height: 520px;
        /*        display: block;*/
        .container {
            width: 992px;
        }
    }
    @media #{$desktop-large} {
        height: 606px;
        .container {
            width: 1170px;
        }
    }
}

.carousel {
    position: relative;
    margin-left: -15px;
    margin-right: -15px;
    .carousel-items {
        padding: 0;
        margin: 0;
        outline: none;
        -webkit-outline: none;
    }
    .carousel-item {
        height: 295px;
        width: 100%;
        position: relative;
        display: inline-block;
        visibility: hidden;
        outline: none;
        -webkit-outline: none;
        @media #{$phone-wide} {
            height: 282px;
        }
        @media #{$tablet} {
            height: 340px;
        }
        @media #{$desktop} {
            height: 462px;
        }
        @media #{$desktop-large} {
            height: 542px;
        }
        &.current {
            visibility: visible;
        }
        .overlay {
            position: absolute;
            z-index: 1;
            top: -30px;
            right: -110px;
            height: 318px;
            @include animation(reveal-left 1s);
            @media #{$tablet} {
                width: 724px;
                height: 611px;
            }
            img {
                height: 100%;
                @media #{$tablet} {
                    height: 70%;
                }
                @media #{$desktop} {
                    height: 90%;
                }
                @media #{$desktop-large} {
                    height: 100%;
                }
            }
        }
        .background {
            @include animation(fade-shrink-down 1s);
            @include box-shadow(0px 2px 4px rgba(0, 0, 0, 0.2));
            width: 100%;
            height: 100%;
            position: relative;
            overflow: hidden;
            img {
                height: 110%;
                width: auto;
                position: absolute;
                margin-left: -5%;
                @media #{$phone-wide} {
                    height: 140%;
                }
                @media #{$tablet} {
                    height: 140%;
                }
                @media #{$desktop} {
                    height: 110%;
                    width: auto;
                    position: absolute;
                    margin-left: -5%;
                }
                @media #{$desktop-large} {}
            }
        }
        .carousel-icon {
            display: block;
            margin-right: auto;
            @media #{$phone-wide} {
                margin-left: auto;
            }
        }
        .content {
            @include animation(fade-shrink-down 1s);
            position: absolute;
            display: block;
            top: 40px;
            left: 30px;
            width: 70%;
            text-align: left;
            z-index: 3;
            @media #{$phone-wide} {
                text-align: center;
            }
            @media #{$tablet} {
                width: 60%;
                top: 40px;
            }
            h1 {
                color: #E2231A;
                font-size: 18px;
                line-height: 14px;
                width: auto;
                margin: 1em auto 0.3em auto;
                padding-bottom: 0.3em;
                @media #{$phone-wide} {
                    font-size: 16px;
                    line-height: 18px;
                }
                @media #{$tablet} {
                    border-bottom: 1px solid #E2231A;
                    margin: 20px 40px 10px 40px;
                }
                @media #{$desktop} {
                    font-size: 31px;
                    line-height: 35px;
                    width: auto;
                    margin: 1em auto 0.3em auto;
                    padding-bottom: 0.3em;
                }
                .small {
                    font-weight: bold;
                    color: #E2231A;
                    display: block;
                    font-size: 12px;
                    line-height: 100%;
                    @media #{$phone-wide} {
                        line-height: 16px;
                    }
                    @media #{$desktop} {
                        font-size: 25px;
                        line-height: 100%;
                    }
                    &.inline {
                        display: inline;
                    }
                }
            }
            .small-bottom-margin {
                margin: auto auto 15px auto;
            }
            p {
                display: none;
                color: #58585A;
                width: auto;
                margin: auto auto 15px auto;
                font-size: 12px;
                &.noMarginBottom {
                    margin-bottom: 0px !important;
                }
                @media #{$tablet} {
                    display: block;
                    font-size: 14px;
                }
                @media #{$desktop} {
                    color: #58585A;
                    width: 500px;
                    margin: auto auto 40px auto;
                    font-size: 20px;
                    &:lang(fr) {
                        margin: auto auto 16px auto;
                    }
                }
            }
            .button {
                width: auto;
                display: block;
                margin: 0px auto;
                text-align: center;
                font-weight: bold;
                font-size: 12px;
                @media #{$tablet} {
                    width: 300px;
                    font-size: 14px;
                    height: 42px;
                    padding-top: 10px;
                }
                @media #{$desktop} {
                    //width: 145px;
                    width: 50%;
                    margin: 10px auto;
                    text-align: center;
                    font-size: 16px;
                    &:lang(fr) {
                        width: 254px;
                        height: auto;
                        //text-align: center;
                    }
                }
                @media #{$desktop-large} {}
            }
        }
    }
    .carousel-pagination {
        list-style: none;
        padding: 0 4px;
        background-color: $ct-red;
        display: inline-block;
        @include border-radius(15px);
        @include box-shadow(0px 2px 4px rgba(0, 0, 0, 0.2));
        margin: 0;
        position: absolute;
        z-index: 3;
        bottom: 28px;
        left: 50%;
        margin: -32px;
        @media #{$phone-wide} {
            bottom: 21px;
        }
        @media #{$tablet} {
            bottom: 21px;
        }
        @media #{$desktop} {
            bottom: 21px;
        }
        @media #{$desktop-large} {
            bottom: 21px;
        }
        li {
            display: inline-block;
            height: 10px;
            width: 10px;
            @include border-radius(6px);
            background-color: $ct-red;
            margin: 0 2px;
            border: 2px solid #fff;
            cursor: pointer;
            &.current {
                background-color: #fff;
            }
            &.carousel-pagination-pause {
                margin: 0 4px 0 10px;
                border-radius: 0px;
                background-color: #fff;
                &.paused {
                    width: 0px;
                    height: 0px;
                    border-top: 5px solid $ct-red;
                    border-bottom: 5px solid $ct-red;
                    border-left: 10px solid #fff;
                    border-right: 0px solid $ct-red;
                }
            }
        }
    }
    .large-right-arrow,
    .large-left-arrow {
        position: absolute;
        z-index: 5;
        top: 50%;
        margin-top: -20px;
        display: none;
        @media #{$tablet} {
            display: block;
        }
    }
    .large-right-arrow {
        right: 0px;
    }
    .large-left-arrow {
        left: 0px;
    }
}


