/* left default states -----------------*/

.carousel .carousel-item.current.overlay,
.carousel-item.current .background,
.carousel-item.current.content {
    transition: none;
}

.carousel .carousel-item.current.left .overlay {
    transform: translateX(-15%);
}

.carousel .carousel-item.current.left .background {
    transform: translateX(-5%);
}


/* right default states */

.carousel .carousel-item.current.right .overlay {
    transform: translateX(15%);
}

.carousel .carousel-item.current.right .background {
    transform: translateX(5%);
}


/*---------------------------------------*/

.carousel .carousel-item.current.out .overlay,
.carousel-item.current.out .background,
.carousel-item.current.out .content,
.carousel .carousel-item.current.in .overlay,
.carousel-item.current.in .background,
.carousel-item.current.in .content {
    opacity: 0;
}


/* animation classes ---------------------*/

.carousel .carousel-item.current.animate .overlay,
.carousel-item.current.animate .background {
    -webkit-transition: transform 0.8s cubic-bezier(0.17, 0.3, 0.34, 0.92), opacity 0.8s;
    -moz-transition: transform 0.8s cubic-bezier(0.17, 0.3, 0.34, 0.92), opacity 0.8s;
    -o-transition: transform 0.8s cubic-bezier(0.17, 0.3, 0.34, 0.92), opacity 0.8s;
    transition: transform 0.8s cubic-bezier(0.17, 0.3, 0.34, 0.92), opacity 0.8s;
}

.carousel .carousel-item.current.animate .content {
    -webkit-transition: opacity 0.4s;
    -moz-transition: opacity 0.4s;
    -o-transition: opacity 0.4s;
    transition: opacity 0.4s;
}


