.vid-thumb {
    &:after {
        display: none; /* remove standard <a> hover */
    }
    background-color: #ccc;
    width: 260px;
    height: 152px;
    border: 1px solid #fff;
    @include box-shadow(0px 2px 1px rgba(0,0,0,0.2));
    position: relative;
    margin: 15px auto;
    overflow: hidden;
    display: block;
    @media #{$phone-wide-only} {
        width: 198px;
        height: 116px;
        margin: 0 15px 15px 0;
        display: inline-block;
    }
    @media #{$tablet} {
        margin: 0 15px 15px 0;
        display: inline-block;
    }
    .btn-play {
        @include sprite;
        background-position: 0px -159px;
        opacity: 0.9;
        margin-top: -35px;
        margin-left: -35px;
        width: 69px;
        height: 69px;
        @include transition(opacity 0.2s, top 0.4s ease);
        z-index: 3;
        position: absolute;
        top: 50%;
        left: 50%;
        @media #{$phone-wide-only} {
            margin-top: -20px;
            margin-left: -20px;
            width: 40px;
            height: 41px;
            background-position: -71px -158px;
        }
        @media #{$tablet} {
            margin-top: -35px;
            margin-left: -35px;
            width: 69px;
            height: 69px;
            background-position: 0px -159px;
        }
    }
    .gradient-overlay {
        background-image:url(../img/vidthumb-gradient-overly.png);
        background-repeat: repeat-x;
        background-size: auto 100%;
        height: 100%;
        width: 100%;
        position: absolute;
        top: 0px;
        left: 0px;
        z-index: 2;
        opacity: 1;
        @include transition(opacity 0.2s);
    }
    p {
        position: absolute;
        bottom: 8px;
        left: 15px;
        z-index: 3;
        color: #fff;
        @include text-shadow(0px 1px 1px  rgba(0,0,0,0.7));
        margin: 0;
        font-size: 16px;
        @media #{$phone-wide-only} {
            font-size: 12px;
        }
    }
    img {
        position: absolute;
        top: 0px;
        left: 0px;
        width: 100%;
        height: 100%;
        transform: scale(1);
        @include transition(transform 0.5s ease);
    }
    &:hover {
        .btn-play {
            opacity: 1;
            top: 45%;
        }
        .gradient-overlay {
            opacity: 0.5;
        }
        img {
            transform: scale(1.2);
        }
    }
}