/* colors */
$ct-red: #c00;
$ct-gray: #444444;
$ct-light-gray: #BCBBBA;
$ct-body: #58585A;

/* mixins */
@mixin sprite {
    background-image: url(../img/spriteboard.png);
    background-repeat: no-repeat;
    background-position: 0px 0px;
    background-size: 778px 455px;
}

@mixin sprite-steps {
    background-image: url(../img/spriteboard-steps-small.png);
    background-repeat: no-repeat;
    background-position: 0px 0px;
    background-size: 108px 297px;
    @media #{$tablet} {
        background-image: url(../img/spriteboard-steps.png);
        background-size: 175px 482px;
    }
}

@mixin animation($var...) {
    -webkit-animation: $var;
    -moz-animation: $var;
    -ms-animation: $var;
    -o-animation: $var;
    animation: $var;
}

@mixin box-shadow($var...) {
    -webkit-box-shadow: $var;
    -moz-box-shadow: $var;
    -ms-box-shadow: $var;
    -o-box-shadow: $var;
    box-shadow: $var;
}

@mixin border-radius($var...) {
    -webkit-border-radius: $var;
    -moz-border-radius: $var;
    -ms-border-radius: $var;
    -o-border-radius: $var;
    border-radius: $var;
}

@mixin text-shadow($var...) {
    -webkit-text-shadow: $var;
    -moz-text-shadow: $var;
    -ms-text-shadow: $var;
    -o-text-shadow: $var;
    text-shadow: $var;
}

@mixin text-shadow($var) {
    -webkit-text-shadow: $var;
    -moz-text-shadow: $var;
    -ms-text-shadow: $var;
    -o-text-shadow: $var;
    text-shadow: $var;
}

@mixin transition($var...) {
    -webkit-transition: $var;
    -moz-transition: $var;
    -ms-transition: $var;
    -o-transition: $var;
    transition: $var;
}

/* break points */
$phone-wide: "only screen and (min-width : 480px)";
$tablet: "only screen and (min-width : 768px)";
$desktop: "only screen and (min-width : 992px)";
$desktop-large: "only screen and (min-width : 1200px)";

$phone-only: "only screen and (min-width : 0px) and (max-width : 479px)";
$phone-wide-only: "only screen and (min-width : 480px) and (max-width : 767px)";
$tablet-only: "only screen and (min-width : 768px) and (max-width : 991px)";