.newsletter {
    background-image: url(../img/envelope-lg.jpg);
    background-size: 357px 192px;
    min-height: 192px;
    overflow: hidden;
    padding: 33px 33px 0 33px;
    background-repeat: no-repeat;
    position: relative;
    margin-top: 20px;
    background-size: 100% 100%;
    
            @media #{$tablet} {
           
            }
            @media #{$desktop} {
            margin-top: 14px;
            background-size: 100% 100%;    
            }
            @media #{$desktop-large} {
            background-size: 357px 192px;    
            }
    
    h2 {
        color: #fff;
        margin-top: 0;
    }
    p {
        color: #fff;
        width: 70%;
    }
    input[type="text"] {
        padding: 0.6em;
        border: none;
        width: 70%;
        
         @media #{$tablet} {
            width: 257px;    
            }
            @media #{$desktop} {
            width: 70%;
            }    
            @media #{$desktop-large} {
            width: 257px;
            } 
        
        
        &:placeholder {
            color: #a8a9ad;
        
                      
            
        }
    }
    .feedback {
        position: absolute;
        opacity: 0;
        left: -100px;
        bottom: 15px;
        width: 50%;
        @include transition(opacity 1s, left 0.6s ease);
        margin: 0;
        p {
            font-size: 18px;
            font-weight: bold;
        }
    }
    .button-set {
        position: absolute;
        @include transition(opacity 1s, left 0.6s ease);
        opacity: 1;
        left: 33px;
        bottom: 33px;
        width: 100%;
    }
    &.complete {
        .feedback {
            left: 33px;
            opacity: 1;
        }
        .button-set {
            opacity: 0;
            left: 300px;
        }
    }
}