.carousel-contest, .carousel-contest-2 {
    .overlay {
        left: -90px;
        top: -10px;
        @media #{$tablet} {
            right: 543px;

        }
        @media #{$desktop} {


        }
        @media #{$desktop-large} {


        }
        img {
            @media #{$desktop} {
                height: 100%;
            }
        }
    }
    .carousel-icon {
        height: 70px;
        width: 105px;
        margin-right: 0;
        margin-left: auto;
        @media #{$phone-wide} {
            margin-right: auto;
        }
        @media #{$desktop} {
            width: 160px;
            height: 105px;
        }
    }
    .content {
        text-align: right;
        left: auto;
        right: 30px;
        @media #{$phone-wide} {
            text-align: center;
            width: 60%;
            top: 40px;
            left: auto;
            right: 30px;
        }
        @media #{$tablet} {
            width: 50%;
            top: 70px;
            &:lang(fr) {
              width: 55%;
            }
        }
        @media #{$desktop} {
            width: 50%;
            top: 40px;
            left: 434px;
            &:lang(fr) {
              top: 10px;
              width: 50%;
            }
        }
        @media #{$desktop-large} {
            text-align: center;
            width: 64%;
            top: 80px;
            left: 415px;
            &:lang(fr) {
              top: 40px;
              width: 65%;
            }
        }
        h1 {
            @media #{$desktop-large} {
                width: 550px;
                &:lang(fr) {
                  width: 500px;
                }
            }
        }

        p {
            @media #{$desktop} {
                margin: auto auto 15px auto;
                font-size: 20px;
                &:lang(fr) {
                  width: 430px;
                }
            }
        }

        .button {
            @media #{$desktop} {
                width: 310px;
                &:lang(fr) {
                    width: 310px;
                }
            }
        }

    }
}
