.feature {
    
    position: relative;
    margin-top: 54px;    
    padding: 0px 42px 25px 42px;
    @media #{$tablet} {
        padding: 0px 42px 65px 42px;
        margin-bottom: 90px;
        height: 360px;
        padding: 0 12px;
        
        @media #{$desktop} {
         padding: 0 42px;   
}    
    }
    
    h2 {
        opacity: 0;
        text-align: center;
        color: $ct-gray;
        font-size: 20px;
        margin-bottom: 1.2em;
        @include animation(reveal-down 0.8s ease forwards);
    }
    p {
        opacity: 0;
        text-align: center;
        @include animation(reveal-down 0.8s ease forwards);
    }
    .button {
        opacity: 0;
        width: 100%;
        display: block;
        margin: auto;
        @include animation(reveal-down 0.8s ease forwards);
        
            @media #{$tablet} {
                
            position: absolute;
            bottom: 20px;
            left: 50%;
                margin-left: -133px;    
            width:100%;   
            left: 60%;
            }
            @media #{$desktop} {
            width: 266px;
            left: 50%;    
            }
            @media #{$desktop-large} {
            width: 266px;
            left: 50%; 
                
            }
        
    }
    
    .icon {
        opacity: 0;
        margin-left: auto;
        margin-right: auto;
        @include animation(reveal-down 0.8s ease forwards);
    }
    
    &.profit-sharing-plan {
        .icon {
            @include sprite;
            width: 162px;
            height: 82px;
            margin: 0px auto 32px auto;
        }
    }
    &.whats-new {
        .icon {
            @include sprite;
            width: 64px;
            height: 90px;
            background-position: -167px 0px;
            margin: 0px auto 23px auto;
        }
    }
    &.access-account {
        .icon {
            @include sprite;
            width: 121px;
            height: 88px;
            background-position: -236px 0px;
            margin: 0px auto 24px auto;
        }
        &:after {
            display: none;
        }
    }
    &:after {
        content: " ";
        position: absolute;
        top: 0px;
        right: -18px;
        background-image: url(../img/divider-squiggle.png);
        background-repeat: repeat-y;
        background-size: 6px 11px;
        width: 6px;
        /*height: 100%;*/
        @include animation(reveal-line-down 0.8s ease forwards);
        display: none;
            @media #{$tablet} {
                display: block;
                height: 200px;
            }
            @media #{$desktop} {
                display: block;
            }
            @media #{$desktop-large} {
                display: block;
            }
        
        
    }
}

.squiggly-divider-hor-features{
    display: block;
    margin: 10px auto;
    width: 100%;
    height: 5px;
    background-image: url(../img/divider-squiggle-hor.png);
    background-repeat: repeat-x;
    width: 80%;

                @media #{$tablet} {
                display: none;
                width: 100%;
            }
            @media #{$desktop} {
                display: none;
            }
            @media #{$desktop-large} {
                display: none;
            }

}

