.phone-remove-divider.right-squiggly-divider {
    &:after {
        @media #{$phone-only} {
            display: none;
        }
        @media #{$phone-wide-only} {
            display: none;
        }
    }
}


.contact-sidexside {
    margin: 10px 0 40px 0;
    @media #{$phone-wide} {
        margin: 30px 0;
    }
    &:before, &:after {
        clear: both;
        content: " ";
        display: table;
    }
    .icon-wrapper {
        padding-top: 0;
        @media #{$phone-wide} {
            float: left;
            width: 80px;
            padding: 0 15px;
        }
        @media #{$tablet} {
            width: 25%;
        }
        img {
            height: auto;
            width: 50px;
            margin: auto;
            @media #{$phone-wide} {
                height: auto;
                width: 100%;
            }
        }
    }
    .copy-wrapper {
        text-align: center;
        @media #{$phone-wide} {
            text-align: left;
            float: left;
            width: 350px;
            padding: 0 15px;
        }
        @media #{$tablet} {
            float: left;
            width: 75%;
            padding: 0 15px;
        }
    }
    
    h4 {
        font-size: 18px;
        font-weight: bold;
    }
    
    h4,
    p {
        margin: 0.8em 0;
    }
    
    
    &.call {
        .icon-wrapper {
            padding-top: 0px;
            @media #{$phone-wide} {
                padding-top: 62px;
            }
            @media #{$tablet} {
            }
            @media #{$desktop} {
            }
            @media #{$desktop-large} {
                padding-top: 50px;
            }
        }
    }
    &.in-person {
        .icon-wrapper {
            padding-top: 0px;
            @media #{$phone-wide} {
                padding-top: 32px;
            }
            @media #{$tablet} {
            }
            @media #{$desktop} {
            }
            @media #{$desktop-large} {
                padding-top: 20px;
            }
        }
    }
    &.email {
        margin-top: 0px;
        @media #{$tablet} {
            margin-top: 50px;
        }
        .icon-wrapper {
            padding-top: 20px;
            @media #{$tablet} {
            }
            @media #{$desktop} {
            }
            @media #{$desktop-large} {
                padding-top: 0px;
            }
        }
    }
}

.icon-text-sidexside {
    padding: 15px 0;
    margin: 0;
    display: table;
    width: 100%;
    @media #{$tablet} {
        margin: 10px 0;
    }
    .icon-wrapper {
        /*float: left;*/
        width: 40%;
        display: table-cell;
        padding: 0 15px;
        padding-left: 0;
        vertical-align: middle;
        @media #{$phone-wide} {
            width: 30%;
            padding-left: 15px;
        }
    }
    .icon-copy {
        /*float: left;*/
        width: 60%;
        display: table-cell;
        vertical-align: middle;
        padding: 0 15px 0 0;
        @media #{$phone-wide} {
            width: 70%;
            padding: 0 15px;
        }
        p {
            margin-bottom: 0;
        }
    }
    &.rounded-border {
        background-color: #f4f4f4;
        border: 1px solid #a7a9ac;
        @include border-radius(8px);
    }
}

