.carousel-maximize-savings {
    .overlay {
        right: -150px;
        top: -10px;
        @media #{$tablet} {
            right: -340px;
            top: -30px;
        }
        @media #{$desktop} {
            right: -140px;
            top: -30px;
        }
        @media #{$desktop-large} {
            right: -90px;
            top: -30px;
        }
    }
    .carousel-icon {
        height: 84px;
        @media #{$desktop} {
            width: 146px;
            height: 134px;
        }
    }
    .content {
        width: 50%;
        @media #{$tablet} {
            width: 50%;
            top: 70px;
        }
        @media #{$desktop} {
            width: 54%;
            top: 40px;
            left: 27px;
        }
        @media #{$desktop-large} {
            width: 64%;
            top: 80px;
            left: -60px;
        }
        h1 {
            @media #{$desktop} {
                width: 545px;
            }
        }

        .button {

        }

    }
}
