@import 'variables';

body { /* set style of general font and add padding for nav header */
    color: $ct-body;
    font-family: arial, sans-serif;
    font-size: 16px;
    padding-top: 68px;
    background-color: #454545;
    @media #{$desktop} {
        padding-top: 106px;
    }
}

.content-area {
    background-color: #fff;
    padding-bottom: 30px;
    /*padding-top: 30px;*/
}

@import 'typography';
@import 'iconography';
@import 'buttons';
@import 'footer';
@import 'navigation';
@import 'carousel';
@import 'poll';
@import 'quicklinks';
@import 'homefeatures';
@import 'lettersignup';
@import 'animations';
@import 'innerheadings';
@import 'videothumb';
@import 'stepscircles';
@import 'relatedlinks';
@import 'sidexside';
@import 'annualreport';
@import 'trackrecords';
@import 'aboutsteps';
@import 'callouts';
@import 'support';


.check-list {
    list-style: none;
    padding: 0;
    li {
        background-image:url(../img/ul-checklist.png);
        background-size: 31px 31px;
        background-position: 0px 0px;
        background-repeat: no-repeat;
        padding: 0.42em 0;
        padding-left: 40px;
        margin-bottom: 0.5em;
    }
    &.medium-check {
        li {
            background-image:url(../img/ul-checklist-medium.png);
            background-size: 24px 24px;
            background-position: 0px 4px;
        }
    }
}

.squiggly-divider-hor {
    display: block;
    margin: 10px auto;
    width: 100%;
    height: 5px;
    background-image: url(../img/divider-squiggle-hor.png);
    background-repeat: repeat-x;
    @media #{$tablet} {
        margin: 30px auto;
    }
}

.right-squiggly-divider {
    &:after {
        content: " ";
        position: absolute;
        top: 0px;
        right: 5px;
        background-image: url(../img/divider-squiggle.png);
        background-repeat: repeat-y;
        background-size: 6px 11px;
        width: 6px;
        height: 100%;
    }
}

.support-sunlife-cta {
    .sign-in-btn {
        a {
            position: relative;
            width: auto;
            display: block;
            margin: auto;
            @media #{$tablet} {
                width: 100%;
                top: 12px;
            }
            @media #{$desktop} {
                top: 20px;
            }
            @media #{$desktop-large} {
                top: 20px;
            }
        }
    }
}

.introduction {
    margin: 30px 0;
}

.phone-wide-half {
    @media #{$phone-wide-only} {
        width: 50%;
        float: left;
    }
    
}

.phone-wide-third {
    @media #{$phone-wide-only} {
        width: 33.3333333%;
        float: left;
    }
    
}

.overflow-hidden {
    overflow: hidden;
}
.bottom-nav {
    background-color: #F4F4F4;
    border: 1px solid #D1D3D4;
    padding: 6px;
    height: auto;
}
