.footer {
    background-color: #454545;
    color: #fff;
    position: relative;
    padding: 10px 0;
    font-size: 18px;
    @media #{$tablet} {
        padding: 40px 0;
    }
    &:after {
        content: " ";
        width: 100%;
        height: 3px;
        position: absolute;
        top: -3px;
        background-color: rgba(69,69,69,0.25);
    }
    
    p {
        font-size: 12px;
        @media #{$tablet} {
            font-size: 14px;
        }
    }
    
    .footer-links {
        list-style: none;
        padding: 0;
        position: relative;
        display: none;
        @media #{$tablet} {
            left: -1em;
        }
        li {
            
            @media #{$tablet} {
                padding: 0 1em;
                display: inline-block;
                border-bottom: none;
                border-right: 1px solid #fff;
            }
            &.last {
                border:none;
            }
        }
        a {
            color: #fff;
            padding: 10px 0;
            border-bottom: 1px solid #fff;
            display: block;
            @media #{$tablet} {
                padding: 0;
                border-bottom: none;
            }
            &:after {
                display: none;
            }
            &:hover {
                text-decoration: underline;
            }
        }
    }
    
    .legal {
        color: #fff;
        text-align: left;
        font-size: 14px;
        
        
        @media #{$tablet} {
            font-size: 18px;
            text-align: right;
            
        }


    }

    a.powered{
        float: right;
        color: white;
        
         &:hover, &:focus {
        color:#fff;
        text-decoration: none;
        /*outline: none;*/
        &:after {
            width: 100%;
            height: 1.4em;
            padding: 0px 3px;
            box-sizing: initial;
            content: " ";
            z-index: -1;
            top: -0.1em;
            left: -0.2em;
            
             background-color: rgba(0,0,0,.0001);
            position: absolute;
            @include transition(width 0.2s ease, background-color 0.1s);
            @include border-radius(4px);
        }
    }
        
           
}
    


}

