.carousel-earned-through-profit-sharing {
    .overlay {
        left: -90px;
        top: -10px;
        @media #{$tablet} {
            right: 543px;

        }
        @media #{$desktop} {


        }
        @media #{$desktop-large} {


        }
        img {
            @media #{$desktop} {
                height: 100%;
            }
        }
    }
    .carousel-icon {
        height: 80px;
        width: 76px;
        margin-right: 0;
        margin-left: auto;
        @media #{$phone-wide} {
            margin-right: auto;
        }
        @media #{$desktop} {
            width: 105px;
            height: 116px;
        }
    }
    .content {
        text-align: right;
        left: auto;
        right: 30px;
        @media #{$phone-wide} {
            text-align: center;
            width: 60%;
            top: 40px;
            left: auto;
            right: 30px;
        }
        @media #{$tablet} {
            width: 50%;
            top: 70px;
        }
        @media #{$desktop} {
            width: 50%;
            top: 40px;
            left: 434px;
        }
        @media #{$desktop-large} {
            width: 64%;
            top: 80px;
            left: 415px;
        }
        h1 {
            @media #{$desktop-large} {
                width: 575px;
            }
        }
        p {
            @media #{$desktop} {
                margin: auto auto 15px auto;
            }
        }
        .button {
            @media #{$desktop} {
                width: 245px;
                &:lang(fr) {
                    width: 245px;
                }
            }
        }

    }
}
