.poll {
    background-color: #F4F4F4;
    margin-top: 14px;
    margin-bottom: 33px;
    @media #{$tablet} {}
    @media #{$desktop} {
        height: auto;
    }
    @media #{$desktop-large} {}
    h2 {
        color: $ct-gray;
        font-size: 20px;
        margin-top: 0;
        margin-bottom: 10px;
        .poll-icon {
            width: 37px;
            height: 32px;
            background-repeat: no-repeat;
            background-image: url(../img/poll-icon.png);
            display: inline-block;
            margin-right: 10px;
        }
    }
    a {
        margin: 0.8em 0 0 0;
        display: block;
        font-size: 14px;
        font-weight: normal;
    }
}

.poll-options {
    display: none;
    position: relative;
    .poll-option {
        cursor: pointer;
        -webkit-user-select: none;
        margin-top: 10px;
        margin-bottom: 10px;
        
        @media #{$tablet} {
            margin-top: 0px;
        margin-bottom: 0px;
            
    }
        @media #{$desktop} {
//            width: 32%;
//            display: inline-block;
            vertical-align: top;
//            text-align: center;
//            margin: 12px 0px;
        }
        @media #{$desktop-large} {}
        .icon-a {
            display: inline-block;
            background-image: url(../img/poll-a.png);
            height: 44px;
            width: 44px;
            background-repeat: no-repeat;
            &:focus,
            &:hover, &.selected {
                background-image: url(../img/poll-a-on.png)
            }
        }
        .icon-b {
            display: inline-block;
            background-image: url(../img/poll-b.png);
            height: 44px;
            width: 44px;
            background-repeat: no-repeat;
            &:focus,
            &:hover, &.selected {
                background-image: url(../img/poll-b-on.png)
            }
        }
        .icon-c {
            display: inline-block;
            background-image: url(../img/poll-c.png);
            height: 44px;
            width: 44px;
            background-repeat: no-repeat;
            &:focus,
            &:hover, &.selected {
                background-image: url(../img/poll-c-on.png)
            }
        }
        .icon-d {
            display: inline-block;
            background-image: url(../img/poll-d.png);
            height: 44px;
            width: 44px;
            background-repeat: no-repeat;
            &:focus,
            &:hover, &.selected {
                background-image: url(../img/poll-d-on.png)
            }
        }
        .icon-e {
            display: inline-block;
            background-image: url(../img/poll-e.png);
            height: 44px;
            width: 44px;
            background-repeat: no-repeat;
            &:focus,
            &:hover, &.selected {
                background-image: url(../img/poll-e-on.png)
            }
        }
        .text {
            display: inline-block;
            vertical-align: middle;
            margin-top: -32px;
            margin-left: 10px;
            width: 70%;
            @media #{$tablet} {
                
                
}
            @media #{$desktop} {
//                display: block;
//                margin-top: 0px;
//                margin-left: 0px;
//                width: auto;
            }
            @media #{$desktop-large} {}
        }
    }
}

.poll-results {
    display: none;
    
    .poll-result {
        margin: 10px 0px;
        .header {
            overflow: auto;
            margin-bottom: 8px;
            .arrow {
                background-image: url(../img/poll-arrow.png);
                background-repeat: no-repeat;
                width: 14px;
                height: 12px;
                float: left;
                margin-top: 4px;
                margin-right: 8px;
                display: none;
            }
            .text {
                float: left;
                font-size: 14px;
            }
            .percent {
                float: right;
                font-size: 14px;
                font-weight: bold;
                color: $ct-red;
            }
        }
        .poll-bar {
            width: 100%;
            height: 5px;
            background-color: $ct-light-gray;
        }
        .poll-bar-fill {
            width: 50%;
            height: 5px;
            background-color: $ct-red;
        }
    }
}