.carousel-profit-sharing-microsite {
    .overlay {
        right: -132px;
        top: -10px;
        @media #{$tablet} {
            right: -381px;
            top: 20px;
        }
        @media #{$desktop} {
            right: -165px;
            top: -30px;
        }
        @media #{$desktop-large} {


        }
        img {
            @media #{$phone-wide} {
                height: 104%;
            }
            @media #{$tablet} {
                height: 341px;
            }
            @media #{$desktop} {
                height: 611px;
            }
            @media #{$desktop-large} {

            }
        }
    }
    .carousel-icon {
        width: 136px;
        margin-top: 10px;
        @media #{$phone-wide} {
            width: 94px;
        }
        @media #{$tablet} {
            width: 200px;
        }
        @media #{$desktop} {}
        @media #{$desktop-large} {}
    }
    .content {
        width: 90%;
        top: 0px;
        left: 14px;
        @media #{$phone-wide} {
            left: 25px;
        }
        @media #{$tablet} {
            width: 64%;
            top: 40px;
        }
        @media #{$desktop} {}
        @media #{$desktop-large} {}

        .content-txt {
            width: 60%;
            @media #{$tablet} {
                width: 100%;
            }
            @media #{$desktop} {
                top: 20px;
            }
        }
        h1 {
            color: #fff;
            padding-bottom: 6px;
            @media #{$phone-wide} {
                line-height: 14px;
            }
            @media #{$tablet} {
                border-bottom: 1px solid #fff;
                line-height: 100%;
            }

            .small {
                line-height: 16px;
                color: white;
                @media #{$desktop} {
                    font-size: 25px;
                    line-height: 35px;
                }
            }
        }
        p {
            color: #fff;
            margin: auto auto auto auto;
            @media #{$desktop} {
                width: 546px;
                &:lang(fr) {
                    margin: auto auto auto auto;
                }
            }
            @media #{$desktop-large} {}
        }
        .button {
            margin: 10px auto;
            text-align: left;
            font-weight: bold;
            font-size: 12px;
            width: 98%;
            display: inline-block;
            height: 44px;
            padding-right: 15%;
            background-color: rgba(0, 0, 0, 0.2);
            margin-bottom: -6px;
            margin-top: 8px;
            padding-top: 13px;
            @media #{$phone-wide} {
                float: left;
                padding-right: 10%;
                width: 68%;
            }
            @media #{$tablet} {
                float: none;
                width: 366px;
                font-size: 14px;
                display: block;
                height: 42px;
                padding-top: 10px;
                padding-right: 0px;
                background-color: none;
                margin-bottom: 10px;
            }
            @media #{$desktop} {
                font-size: 16px;
                width: 366px;
                display: block;
                height: 44px;
                margin: 10px auto;
                text-align: left;
                font-weight: bold;
                padding-top: 10px;
                margin-bottom: auto;
                &:lang(fr) {
                    width: 366px;
                    height: 44px;
                    text-align: left;
                }
            }
            @media #{$desktop-large} {
                font-size: 16px;
                width: 366px;
                display: block;
            }
            .arrow-btn {
                position: absolute;
                top: 5px;
                right: 7px;
                @media #{$tablet} {}
                @media #{$desktop} {}
                @media #{$desktop-large} {}
            }
            .vid-thmb {
                height: 44px;
                width: 44px;
                position: absolute;
                top: 0px;
                right: 0px;
                &.plan-overview {
                    background-image: url(../img/carousel/thmb-plan-overview.jpg);
                }
                &.manage-accounts {
                    background-image: url(../img/carousel/thmb-manage-accounts.jpg);
                }
            }
        }
    }
}
