.support {
    h2 {
        text-align: center;
        @media #{$phone-wide} {
            text-align: left;
        }
    }
    .sign-in-btn {
        margin-bottom: 30px;
        margin: 0 auto 30px auto;
        max-width: 300px;
        @media #{$phone-wide} {
            margin-left: 0;
        }
        @media #{$tablet} {
            margin: 0 auto 0 auto;
        }
    }
}