/* used to create a filled circle animation within a button */
@-webkit-keyframes circle-fill {
  0%   { opacity: 0; transform: scale(0); }
  10%  { opacity: 1; }
  100% { opacity: 1; transform: scale(2); }
}
@-moz-keyframes circle-fill {
  0%   { opacity: 0; transform: scale(0); }
  30%  { opacity: 1; }
  100% { opacity: 1; transform: scale(2); }
}
@-o-keyframes circle-fill {
  0%   { opacity: 0; transform: scale(0); }
  30%  { opacity: 1; }
  100% { opacity: 1; transform: scale(2); }
}
@keyframes circle-fill {
  0%   { opacity: 0; transform: scale(0); }
  10%  { opacity: 1; }
  100% { opacity: 1; transform: scale(2); }
}

/* makes an element wiggle left and right */
@-webkit-keyframes wiggle-lr {
  0%    { transform: translate(0px, 0px); }
  25%   { transform: translate(4px, 0px); }
  32%   { transform: translate(-3px, 0px); }
  36%   { transform: translate(0px, 0px);}
  100%  { transform: translate(0px, 0px); }
}
@-moz-keyframes wiggle-lr {
  0%    { transform: translate(0px, 0px); }
  25%   { transform: translate(4px, 0px); }
  32%   { transform: translate(-3px, 0px); }
  36%   { transform: translate(0px, 0px);}
  100%  { transform: translate(0px, 0px); }
}
@-o-keyframes wiggle-lr {
  0%    { transform: translate(0px, 0px); }
  25%   { transform: translate(4px, 0px); }
  32%   { transform: translate(-3px, 0px); }
  36%   { transform: translate(0px, 0px);}
  100%  { transform: translate(0px, 0px); }
}
@keyframes wiggle-lr {
  0%    { transform: translate(0px, 0px); }
  25%   { transform: translate(4px, 0px); }
  32%   { transform: translate(-3px, 0px); }
  36%   { transform: translate(0px, 0px);}
  100%  { transform: translate(0px, 0px); }
}

/* used to reveal squiggly line dividers downward */
@-webkit-keyframes reveal-line-down {
  0%   { height: 0; }
  100%  { height: 100%; }
}
@-moz-keyframes reveal-line-down {
  0%   { height: 0; }
  100%  { height: 100%; }
}
@-o-keyframes reveal-line-down {
  0%   { height: 0; }
  100%  { height: 100%; }
}
@keyframes reveal-line-down {
  0%   { height: 0; }
  100%  { height: 100%; }
}

/* used to fade-in any element */
@-webkit-keyframes fade-in {
  0%    { opacity: 0; }
  100%  { opacity: 1; }
}
@-moz-keyframes fade-in {
  0%    { opacity: 0; }
  100%  { opacity: 1; }
}
@-o-keyframes fade-in {
  0%    { opacity: 0; }
  100%  { opacity: 1; }
}
@keyframes fade-in {
  0%    { opacity: 0; }
  100%  { opacity: 1; }
}

/* used to fade-in and shift down any element */
@-webkit-keyframes reveal-down {
  0%    { transform: translate(0px, -20px); opacity: 0; }
  100%  { transform: translate(0px, 0px); opacity: 1; }
}
@-moz-keyframes reveal-down {
  0%    { transform: translate(0px, -20px); opacity: 0; }
  100%  { transform: translate(0px, 0px); opacity: 1; }
}
@-o-keyframes reveal-down {
  0%    { transform: translate(0px, -20px); opacity: 0; }
  100%  { transform: translate(0px, 0px); opacity: 1; }
}
@keyframes reveal-down {
  0%    { transform: translate(0px, -20px); opacity: 0; }
  100%  { transform: translate(0px, 0px); opacity: 1; }
}

/* fade in and shrink to size any item */
@-webkit-keyframes fade-shrink-down {
  0%    { transform: scale(1.1); opacity: 0; }
  100%  { transform: scale(1); opacity: 1; }
}
@-moz-keyframes fade-shrink-down {
  0%    { transform: scale(1.1); opacity: 0; }
  100%  { transform: scale(1); opacity: 1; }
}
@-o-keyframes fade-shrink-down {
  0%    { transform: scale(1.1); opacity: 0; }
  100%  { transform: scale(1); opacity: 1; }
}
@keyframes fade-shrink-down {
  0%    { transform: scale(1.1); opacity: 0; }
  100%  { transform: scale(1); opacity: 1; }
}

/* fade in and shift from right */
@-webkit-keyframes reveal-left {
  0%    { transform: translate(20px, 0px); opacity: 0; }
  100%  { transform: translate(0px, 0px); opacity: 1; }
}
@-moz-keyframes reveal-left {
  0%    { transform: translate(20px, 0px); opacity: 0; }
  100%  { transform: translate(0px, 0px); opacity: 1; }
}
@-o-keyframes reveal-left {
  0%    { transform: translate(20px, 0px); opacity: 0; }
  100%  { transform: translate(0px, 0px); opacity: 1; }
}
@keyframes reveal-left {
  0%    { transform: translate(20px, 0px); opacity: 0; }
  100%  { transform: translate(0px, 0px); opacity: 1; }
}

/* fade in and shift from left */
@-webkit-keyframes reveal-right {
  0%    { transform: translate(-20px, 0px); opacity: 0; }
  100%  { transform: translate(0px, 0px); opacity: 1; }
}
@-moz-keyframes reveal-right {
  0%    { transform: translate(-20px, 0px); opacity: 0; }
  100%  { transform: translate(0px, 0px); opacity: 1; }
}
@-o-keyframes reveal-right {
  0%    { transform: translate(-20px, 0px); opacity: 0; }
  100%  { transform: translate(0px, 0px); opacity: 1; }
}
@keyframes reveal-right {
  0%    { transform: translate(-20px, 0px); opacity: 0; }
  100%  { transform: translate(0px, 0px); opacity: 1; }
}

/* shake any item up and down */
@-webkit-keyframes shake-vertical {
  0%    { transform: translate(0px, 0px); }
  25%   { transform: translate(20px, 0px); }
  25%   { transform: translate(-20px, 0px); }
  100%  { transform: translate(0px, 0px); }
}
@-moz-keyframes shake-vertical {
  0%    { transform: translate(0px, 0px); }
  25%   { transform: translate(20px, 0px); }
  25%   { transform: translate(-20px, 0px); }
  100%  { transform: translate(0px, 0px); }
}
@-o-keyframes shake-vertical {
  0%    { transform: translate(0px, 0px); }
  25%   { transform: translate(20px, 0px); }
  25%   { transform: translate(-20px, 0px); }
  100%  { transform: translate(0px, 0px); }
}
@keyframes shake-vertical {
  0%    { transform: translate(0px, 0px); }
  25%   { transform: translate(20px, 0px); }
  25%   { transform: translate(-20px, 0px); }
  100%  { transform: translate(0px, 0px); }
}


/* animates the circle within buttons */
@-webkit-keyframes circle-press {
  0%   { opacity: 0; transform: scale(0); }
  10%  { opacity: 1; transform: scale(0.1); }
  90%  { opacity: 1; transform: scale(0.9); }
  100% { opacity: 1; transform: scale(1); }
}
@-moz-keyframes circle-press {
  0%   { opacity: 0; transform: scale(0); }
  10%  { opacity: 1; transform: scale(0.1); }
  90%  { opacity: 1; transform: scale(0.9); }
  100% { opacity: 1; transform: scale(1); }
}
@-o-keyframes circle-press {
  0%   { opacity: 0; transform: scale(0); }
  10%  { opacity: 1; transform: scale(0.1); }
  90%  { opacity: 1; transform: scale(0.9); }
  100% { opacity: 1; transform: scale(1); }
}
@keyframes circle-press {
  0%   { opacity: 0; transform: scale(0); }
  10%  { opacity: 1; }
  70%  { opacity: 1; }
  100% { opacity: 1; transform: scale(1); }
}

/* fades out the circle within buttons */
@-webkit-keyframes circle-release {
  0%   { opacity: 1;  }
  100% { opacity: 0;  }
}
@-moz-keyframes circle-release {
  0%   { opacity: 1;  }
  100% { opacity: 0;  }
}
@-o-keyframes circle-release {
  0%   { opacity: 1;  }
  100% { opacity: 0;  }
}
@keyframes circle-release {
  0%   { opacity: 1;  }
  100% { opacity: 0;  }
}


.btn-press {
    position: absolute;
    background-color: rgba(100,100,100,0.3);
    @include animation(circle-press 0.3s forwards);
    animation-timing-function: cubic-bezier(1, 0.01, 0.76, 0.99);
    @include border-radius(50%);
    z-index: -1;
}
.btn-release {
    @include animation(circle-release 0.3s forwards);
}

.ghost {
    .btn-press {
        background-color: rgba(255,255,255,1);
    }
}

.error {
    @include animation(shake-vertical 0.2s forwards);
}

.reveal-down {
    opacity: 0;
    transform: translate(0px, -20px);
    &.animate {
        @include animation(reveal-down 1s forwards);
    }
}
.reveal-shrink {
    opacity: 1;
    &.animate {
        @include animation(fade-shrink-down 1s forwards);
    }
}