.callout {
    color: #fff;
    position: relative;
    h3 {
        color: #fff;
        font-weight: bold;
        font-size: 34px;
        @media #{$desktop} {
            font-size: 40px;
        }
    }
    &.more-on-investing-callout {
        background-image: url(../img/callout-mechanic-man.jpg);
        background-size: cover;
        background-repeat: no-repeat;
        height: 280px;
        padding: 30px;
        text-align: center;
        margin-left: -15px;
        margin-right: -15px;
        @media #{$phone-wide} {
            height: 300px;
            text-align: left;
            background-position: -220px -80px;
            background-size: 960px auto;
            margin-left: 0px;
            margin-right: 0px;
        }
        @media #{$tablet} {
            text-align: left;
            height: 330px;
            background-size: 100% auto;
            background-position: 0px -40px;
        }
        @media #{$desktop} {
            height: 452px;
            background-position: -180px -56px;
            background-size: auto;
        }
        &:after {
            content: " ";
            background-color: black;
            opacity: 0.3;
            position: absolute;
            top: 0px;
            left: 0px;
            width: 100%;
            height: 100%;
            @media #{$tablet} {
                opacity: 0.1;
            }
            @media #{$desktop} {
                display: none;
            }
        }
        h3 {
            margin: 0 0 0.2em 0;
        }
        .content-wrapper {
            position: relative;
            z-index: 2;
            max-width: 500px;
            @media #{$phone-wide} {
                position: absolute;
                bottom: 30px;
            }
            @media #{$tablet} {
                margin-right: 30px;
                position: absolute;
                bottom: 25px;
                left: 30px;
            }
        }
    }

    &.profit-sharing-callout {
/*        background-image: url(../img/callout-mountains.jpg);
        background-size: cover;
        background-position: bottom;*/
        background-color: #58585A;
        margin-top: 30px;
        padding: 10px;
        p {
            padding: 15px 0;
            margin: 0;
        }
        .button {
            margin: 15px 0;
            width: 100%;
            display: block;
        }
    }
    
}