.carousel-track-report {
    .overlay {
        right: -150px;
        top: -10px;
        @media #{$tablet} {
            right: -340px;
            top: -30px;
        }
        @media #{$desktop} {
            right: -140px;
            top: -30px;
        }
        @media #{$desktop-large} {
            right: -90px;
            top: -30px;
        }
    }
    .carousel-icon {
        width: 60px;
        height: auto;
        @media #{$phone-wide} {
            width: 60px;
            height: auto;
        }
        @media #{$desktop} {
            width: 88px;
            height: auto;
        }
    }
    .content {
        width: 50%;
        @media #{$tablet} {
            width: 50%;
            top: 70px;
        }
        @media #{$desktop} {
            width: 54%;
            top: 40px;
            left: 27px;
        }
        @media #{$desktop-large} {
            width: 64%;
            top: 80px;
            left: -5px;
        }
        h1 {
            @media #{$desktop} {
                width: 545px;
                &:lang(fr) {
                    width: 450px;
                }
            }
        }
        p {
            width: 470px;
            @media #{$tablet} {
                width: auto;
            }
            @media #{$desktop} {
                width: 470px;
            }
        }
        .button {
            height: auto;
            width: 100%;
            margin: 0px;
            @media #{$tablet} {
                width: 80%;
                margin: 0px auto;
                &:lang(fr) {
                    width: 80%;
                }
            }
            @media #{$desktop} {
                width: 70%;
                &:lang(fr) {
                    width: 70%;
                }
            }
            @media #{$desktop-large} {
                width: 50%;
            }
        }
    }
}