.heading-graphic {
    position: relative;
    height: 72px;
    overflow: hidden;
    padding: 7px;
    
    &:lang(fr) {
            height: 96px;    
        }
    
    @media #{$phone-wide} {
        height: 106px;
        padding: 13px 0;
        
         &:lang(fr) {
            height: 132px;    
        }   
        
    }
    @media #{$tablet} {
        height: 174px;
        padding: 20px 0;
        
                 &:lang(fr) {
            height: 214px;    
        } 
        
    }
    @media #{$desktop} {
        height: 174px;
        
                 &:lang(fr) {
            height: 220px;    
        } 
        
    }
    @media #{$desktop-large} {
        height: 318px;
        padding: 66px 0;
        
        &:lang(fr) {
            height: 318px;
            padding: 36px 0;    
        }
    }
    h1 {
        color: #fff;
        padding-bottom: 0.1em;
        border-bottom: 3px solid #fff;
        display: block;
        margin: 0px auto;
        text-align: center;
        white-space: nowrap;
        position: relative;
        z-index: 2;
    }
    .heading-icon {
        display: block;
        margin: 0 auto;
        width: 46px;
        position: relative;
        z-index: 2;
        @media #{$phone-wide} {
            width: 67px;
        }
        @media #{$tablet} {
            width: 115px;
        }
        @media #{$desktop} {
            width: 115px;
        }
        @media #{$desktop-large} {
            width: 165px;
        }
    }
    .heading-image {
        width: 110%;
        height: auto;
        position: absolute;
        top: 0;
        max-width: 1920px;
        min-height: 72px;
        left: 0;
        right: 0;
        
        
            &:lang(fr) {
            width: 160%;    
        }
        
        
        @media #{$phone-wide} {
            &:lang(fr) {
            width: 140%;    
        }
        }
        
        @media #{$tablet} {
            width: 110%;
            
            &:lang(fr) {
            width: 130%;    
        } 
            
        }
        @media #{$desktop-large} {
            min-width: 1496px;
        }
    }
    &:after {
        width: 700px;
        position: absolute;
        margin: auto;
        z-index: 1;
        top: -10%;
        left: 50%;
        margin-left: -350px;
        height: 110%;
        content: " ";
        background-image: url(../img/heading-darken.png);
        background-size: 100% 100%;
    }
}


