.home {
    .navigation-header {
        .logo, .nav-list {
            @media #{$desktop} {
                @include animation(reveal-down 1s);
            }
        }
    }
}

.navigation-header {
    height: 68px;
    background-color: #fff;
    padding-top: 12px;
    z-index: 10;
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    
    @media #{$tablet} {
        padding-top: 9px;
    }
    
    @media #{$desktop} {
        height: 106px;
        padding-top: 29px;    
    }
    
    
    &:after {
        content: " ";
        width: 100%;
        height: 3px;
        position: absolute;
        bottom: -3px;
        background-color: rgba(69,69,69,0.25);
    }
    .container {
        position: relative;
    }
    .logo {
        height: 32px;
        float: left;
        margin-left: 15px;
        margin-top: 6px;
        
        @media #{$tablet} {
            height: 50px;
            margin-left: 20px;
            margin-top: 0;
        }
        @media #{$desktop} {
            height: 34px;
        }
        @media #{$desktop-large} {
            height: 44px;
        }
        img {
            display: block;
            width: auto;
            height: 100%;
        }
    }
    .nav-list {
        position: fixed;
        z-index: 50;
        width: 260px;
        right: 0;
        top: 0px;
        background-color: #fff;
        padding: 0;
        min-height: 100vh; /*temp*/
        @include box-shadow(0px 0px 5px rgba(0,0,0,0.5));
        display: none;
        @media #{$desktop} {
            display: block;
            @include box-shadow(none);
            position: static;
            width: auto;
            min-height: 0;
            background-color: transparent;
            float: right;
            list-style: none;
            margin: 0;
            &:lang(fr) {
                       
                @media #{$desktop} {
            width: 644px;
        }
                @media #{$desktop-large} {
            width: 856px;
        }
                
                
                    }
        }
        li {
            display: block;
            text-align: left;
            background-color: #fff;
            @media #{$desktop} {
                display: inline-block;
                text-align: center;
            }
            &.current {
                position: relative;
                border-right: 4px solid $ct-red;
                @media #{$desktop} {
                    border-right: none;
                }
                &:after {
                    @media #{$desktop} {
                        content: " ";
                        display: block;
                        position: absolute;
                        width: 16px;
                        height: 12px;
                        position: absolute;
                        left: 50%;
                        top: 110%;
                        margin-left: -8px;
                        @include sprite;
                        background-position: -360px -4px;
                    }
                }
            }
            a {
                text-transform: uppercase;
                display: block;
                padding: 0.8em;
                color: $ct-gray;
                font-size: 14px;
                font-weight: bold;
                border-bottom: 1px solid #cecece;
                overflow: hidden;
                @include transition(border 1s, background-color 0.2s);
                @media #{$desktop} {
                    &:lang(fr) {
                       font-size: 12px;
                    }
                    @include border-radius(4px);
                    border: 1px solid transparent;
                }
                &:after {
                    display: none;
                }
                &:before {
                    /*opacity: 0;
                    z-index: -1;
                    position: absolute;
                    @include border-radius(500px);
                    width: 0px;
                    height: 0px;
                    content: " ";
                    left: 50%;
                    top: 50%;
                    margin-left: -250px;
                    margin-top: -250px;
                    width: 500px;
                    height: 500px;
                    background-color: #e5e5e5;*/
                    
                }
                &:hover {
                    text-decoration: none;
                    @media #{$desktop} {
                        border: 1px solid #cecece;
                    }
                    /*background-color: #e5e5e5;*/
                    &:before {
                        /*-webkit-animation: circle-fill 1s ease-out forwards;*/
                    }
                }
                &:active {
                    &:before {
                        /*-webkit-animation: circle-fill 1s ease-out forwards;*/
                    }
                }
                &:focus {
                    @media #{$desktop} {
                        border: 1px solid $ct-light-gray;
                        text-decoration: none;
                        outline: none;
                    }
                }
            }
            &.menu-title {
                h2 {
                    font-size: 16px;
                    color: #fff;
                    background-color: $ct-gray;
                    margin: 0;
                    padding: 8px 10px;
                    text-align: left;
                }
                .btn-close {
                    position: absolute;
                    top: 1px;
                    right: 1px;
                    width: 35px;
                    height: 35px;
                    background-image: url(../img/menu-close.png);
                    background-size: 20px 20px;
                    background-repeat: no-repeat;
                    background-position: 8px 7px;
                    border: none;
                }
                @media #{$desktop} {
                    display: none;
                }
            }
        }
    }
    .french-link {
        border-radius: 4px;
        color: #fff;
        background-color: #c00;
        position: absolute;
        top: -24px;
        right: 11px;
        font-size: 14px;
        font-weight: normal;
        padding: 1px 4px;
        display: none;
        
        @media #{$desktop} {
            display: block;
        }
   
        &:hover {
            background-color: #fcebeb;
            color: $ct-gray;    
        }
        &:after{
            display: none;
        }
    }
    .french-mobile-link {
        @media #{$desktop} {
            display: none !important;
        }
    }


}

.btn-mobile-menu {
    width: 38px;
    height: 30px;
    display: block;
    float: right;
    margin-right: 15px;
    margin-top: 6px;
    @media #{$tablet} {
        margin-right: 20px;
        margin-top: 9px;
    }
    @media #{$desktop} {
        display: none;
    }
    &:after {
        display: none;
    }
    .bar {
        width: 100%;
        height: 6px;
        @include box-shadow(1px 1px 0px rgba(0,0,0,0.18));
        background-color: #454545;
        position: absolute;
        left: 0;
        &.top{
            top: 0px;
        }
        &.mid{
            top: 12px;
        }
        &.bot{
            top: 24px;
        }
    }
}

.blocker {
    display: none;
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0px;
    left: 0px;
    z-index: 49;
    background-color: rgba(0,0,0,0.5);
    @media #{$desktop} {
        display: none;
    }
}

.mob-nav-open {
    .blocker {
        display: block;
        @include animation(fade-in 0.4s);
        @media #{$desktop} {
            display: none;
        }
    }
    .navigation-header {
        .nav-list {
            display: block;
            @include animation(reveal-left 0.4s);
        }
    }
}

.only-desktop{
    display: none;
    
        @media #{$desktop} {
            display: block;
        }
        @media #{$desktop-large} {
            display: none;
        }
    
}