.carousel-savings-awards {
    .overlay {
        right: -155px;
        top: -20px;
        @media #{$phone-wide} {
            right: -120px;
            top: -20px;
        }
        @media #{$tablet} {
            right: -230px;
            top: -30px;
        }
        @media #{$desktop} {
            right: -100px;
            top: -30px;
        }
        @media #{$desktop-large} {
            right: 40px;
            top: -40px;
        }
    }
    .carousel-icon {
        height: 84px;
        @media #{$desktop} {
            width: 90px;
            height: 96px;
        }
    }
    .content {
        width: 50%;
        top: 30px;
        @media #{$tablet} {
            width: 50%;
            top: 55px;
        }
        @media #{$desktop} {
            width: 54%;
            top: 50px;
            left: 50px;
        }
        @media #{$desktop-large} {
            width: 64%;
            top: 80px;
            left: -10px;
        }
        h1 {
            @media #{$desktop} {
                width: 545px;
            }
        }

        .button {
            @media #{$desktop} {
                width: 150px;
            }
        }
    }
}
