.carousel-gift-cards {
    .overlay {
        left: -70px;
        top: -10px;
        @media #{$tablet} {
            right: 395px;

        }
        @media #{$desktop} {
            right: 296px;

        }
        @media #{$desktop-large} {
            right: 395px;

        }
        img {
            @media #{$tablet} {
                height: 80%;
            }
            @media #{$desktop} {
                height: 100%;
            }
        }
    }
    .carousel-icon {
        height: 50px;
        @media #{$desktop} {
            width: 144px;
            height: 80px;
        }
    }
    .content {
        text-align: right;
        top: 90px;
        left: auto;
        right: 30px;
        @media #{$phone-wide} {
            text-align: center;
            left: auto;
            right: 30px;
        }
        @media #{$tablet} {
            width: 50%;
            top: 70px;
        }
        @media #{$desktop} {
            width: 64%;
            top: 80px;
            left: 324px;
        }
        @media #{$desktop-large} {
            left: 415px;
        }
        h1 {
            color: #fff;
            @media #{$tablet} {
                border-bottom: 1px solid #fff;
            }
            @media #{$desktop} {
                width: 500px;
            }
            .small {
                color: #fff;
                margin-bottom: 6px;
                @media #{$tablet} {
                    margin-bottom: auto;
                }
            }
        }
        p {
            color: #fff;
        }
        .button {
            @media #{$phone-wide} {}
            @media #{$tablet} {
                width: 300px;
                font-size: 14px;
                display: block;
                height: 42px;
                padding-top: 10px;
            }
            @media #{$desktop} {
                width: 300px;
                text-align: center;
                &:lang(fr) {
                    width: 300px;
                }
            }
        }
        
    }
}
