.button {
    text-transform: uppercase;
    color: #fff;
    font-weight: normal;
    font-size: 16px;
    background-color: $ct-red;
    text-align: center;
    padding: 0.6em 1em;
    border: 1px solid #fff;
    position: relative;
    overflow: hidden;
    @include box-shadow(0px 0px 0px rgba(0,0,0,0));
    &:before {
        opacity: 0;
        z-index: -1;
        position: absolute;
        @include border-radius(500px);
        width: 0px;
        height: 0px;
        content: " ";
        left: 50%;
        top: 50%;
        margin-left: -250px;
        margin-top: -250px;
        width: 500px;
        height: 500px;
        background-color: #eb0000;

    }
    &:hover, &:focus {
        background-color: #dc1111;
        &:before {
            /*-webkit-animation: circle-fill 1s ease-out forwards;*/
        }
        .arrow-btn {
            background-position: -33px -89px;
        }
    }
    &:active {
        &:before {
            /*-webkit-animation: circle-fill 1s ease-out forwards;*/
        }
    }
    &:after {
        display: none; /* clears default a tag hovers */
    }
    &.ghost  {
        background-color: transparent;
        @include transition(background-color 0.2s);
        &:before {
            background-color: #fff;
        }
        &:hover, &:focus {
            background-color: rgba(255,255,255,0.8);
            color: $ct-red;
        }
        
    }
    .arrow-btn {
        @include sprite;
        background-position: -1px -89px;
        height: 30px;
        width: 30px;
        display: inline-block;
        vertical-align: middle;
        margin-left: 1em;
    }
}


.arrow-links {
    a {
        font-weight: normal;
        color: $ct-gray;
        margin-right: 1em;
        &:after {
            content: " ";
            @include sprite;
            width: 5px;
            height: 13px;
            display: inline-block;
            vertical-align: middle;
            background-position: -68px -89px;
            border-radius: 0;
            position: relative;
            top: 0px;
            left: 0px;
            background-color: transparent;
            margin-left: 0.5em;
            margin-right: -2em;
            @include transition(margin-left 0.2s ease);
        }
        &:focus, &:hover {
            color: $ct-red;
            &:after {
                margin-left: 0.7em;
            }
        }
    }
}

.submit-btn-white {
    @include border-radius(0 4px 4px 0);
    width: 36px;
    height: 41px;
    display: inline-block;
    vertical-align: middle;
    transition: background-color 0.2s ease;
    overflow: hidden;
    position: relative;
    .arrow {
        background-image: url(../img/right-arrow.png);
        background-size: 20px 19px;
        background-repeat: no-repeat;
        background-position: 8px 11px;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0px;
        left: 0px;
        z-index: 2;
    }
    &:before {
        opacity: 0;
        z-index: -2;
        position: absolute;
        @include border-radius(500px);
        width: 0px;
        height: 0px;
        content: " ";
        left: 50%;
        top: 50%;
        margin-left: -250px;
        margin-top: -250px;
        width: 500px;
        height: 500px;
        background-color: $ct-gray;

    }
    &:after {
        display: none;
    }
    &:hover, &:focus {
        /*background-color: $ct-gray;*/
        &:before {
            /*-webkit-animation: circle-fill 3s ease-out forwards;*/
        }
        .arrow {
            @include animation(wiggle-lr 2s infinite);
        }
    }
}

/* left and right carousel buttons */
.large-right-arrow,
.large-left-arrow {
    position: relative;
    padding: 20px;
    opacity: 0.5;
    transform: scale(1);
    @include transition(opacity 0.2s, transform 0.2s ease);
    .arrow {
        @include sprite;
        width: 21px;
        height: 38px;
        background-position: -129px -87px;
        display: block;
    }
    &:after {
        display: none; /* removes default <a> hover */
    }
    &:hover, &:focus {
        opacity: 1;
        transform: scale(1.1);
    }
    
}
.large-right-arrow {
    .arrow {
        background-position: -106px -87px;
    }
}
.large-left-arrow {
    .arrow {
        background-position: -129px -87px;
    }
}

.circle-active {
    &:active {
        background: #ccc;
    }
}
