h1 {
    font-size: 18px;
    line-height: 120%;
    font-weight: bold;
    text-transform: uppercase;
    @media #{$phone-wide} {
        font-size: 24px;
    }
    @media #{$tablet} {
        font-size: 40px;
    }
    @media #{$desktop-large} {
        font-size: 54px;
    }
}

h2 {
    font-size: 18px;
    line-height: 120%;
    font-weight: bold;
    /*text-transform: uppercase;*/
    color: $ct-gray;
    margin: 2em 0 1.4em 0;
    @media #{$desktop} {
        font-size: 20px;
    }
    &.red-cap {
        color: $ct-red;
        text-transform: uppercase;
    }
}

h3 {
    font-size: 17px;
    line-height: 120%;
    color: $ct-gray;
    margin: 2.2em 0 1.5em 0;
    @media #{$desktop} {
        font-size: 20px;
    }
}

h4 {
    font-size: 14px;
    line-height: 120%;
    letter-spacing: 1px;
    text-transform: uppercase;
    font-weight: normal;
    color: $ct-gray;
}

p {
    font-size: 14px;
    line-height: 140%;
    font-weight: normal;
    margin-bottom: 1em;
    @media #{$desktop} {
        font-size: 16px;
    }
}

li {
    font-size: 14px;
    @media #{$desktop} {
        font-size: 16px;
    }
}

a {
    color: $ct-gray;
    display: inline-block;
    position: relative;
    @include transition(color 0.2s);
    z-index: 1;
    font-weight: bold;
    &:after {
        width: 0%;
        height: 1.4em;
        padding: 0px 3px;
        box-sizing: initial;
        content: " ";
        z-index: -1;
        top: -0.1em;
        left: -0.2em;
        background-color: transparent;
        position: absolute;
        @include transition(width 0.2s ease, background-color 0.1s);
        @include border-radius(4px);
    }
    &:hover, &:focus {
        color:#fff;
        text-decoration: none;
        /*outline: none;*/
        &:after {
            width: 100%;
            height: 1.4em;
            padding: 0px 3px;
            box-sizing: initial;
            content: " ";
            z-index: -1;
            top: -0.1em;
            left: -0.2em;
            /*background-color: #fcebeb;*/
            background-color: $ct-red;
            position: absolute;
            @include transition(width 0.2s ease, background-color 0.1s);
            @include border-radius(4px);
        }
    }
}

.bold {
    font-weight: bold;
}

.introduction {
    p {
        font-size: 17px;
        @media #{$tablet} {
            font-size: 20px;
        }
    }
    h3 {
        font-weight: bold;
    }
}