.annual-report-wrapper {
    position: relative;
    overflow: hidden;
    padding-left: 25px;
    padding-right: 25px;
    padding-bottom: 21px;
    background-color: #ccc;
    background-image: url(../img/bg-annual-report.jpg);    
    background-size: cover;
    margin-left: -15px;
    margin-right: -15px;
    @media #{$tablet} {
        margin-left: 0;
        margin-right: 0;
    }
    h2 {
        font-size: 30px;
        text-transform: uppercase;
        margin: 18px 0;
        color: #fff;
        @media #{$tablet} {
            font-size: 45px;
        }
        @media #{$desktop-large} {
            font-size: 60px;
        }
        .small {
            font-size: 0.66em;
            color: inherit;
            font-weight: inherit;
        }
    }
    p {
        color: #fff;
        margin-bottom: 0.3em;
    }
    .annual-report-img {
        display: block;
        margin-right: auto;
        width: 450px;
        /*width: 120px;*/
        margin-top: 15px;
        @media #{$tablet} {
            /*width: auto;*/
            position: absolute;
            left: 50%;
            bottom: 19px;
        }
        @media #{$desktop} {
            left: 46%;
            bottom: 76px;
            &:lang(fr) {
                bottom: 160px;
            }
        }
        @media #{$desktop-large} {
            left: 46%;
            bottom: 50px;
        }
    }
    .check-list {
        margin-right: 15px;
        @media #{$tablet} {
            width: 44%;
            margin-right: 0px;
        }
        li {
            margin-bottom: 0;
            color: #fff;
        }
    }
    .button {
        margin-top: 15px;
    }
}

.archive {
    h2 {
        margin-top: 0;
    }
    h3 {
        margin-top: 0px;
        margin-bottom: 15px;
    }
    li {
        margin-bottom: 0.5em;
    }
    margin-top: 30px;
    @media #{$desktop} {
        margin-top: 0;
    }
}

